<template>
	<view class="container">
		<view v-if="loading2==0" class="page-body">
			<view :style="{top:top+'px'}" class='wrapper'>
				<view class="toolbar-box">
					<view v-show="isOpen" class='toolbar' @tap="format">
						<view :class="formats.bold ? 'ql-active' : ''" class="iconfont icon-zitijiacu" data-name="bold"></view>
						<view :class="formats.italic ? 'ql-active' : ''" class="iconfont icon-zitixieti" data-name="italic"></view>
						<view :class="formats.underline ? 'ql-active' : ''" class="iconfont icon-zitixiahuaxian" data-name="underline"></view>
						<view :class="formats.strike ? 'ql-active' : ''" class="iconfont icon-zitishanchuxian" data-name="strike"></view>
						<view :class="formats.align === 'left' ? 'ql-active' : ''" class="iconfont icon-zuoduiqi" data-name="align"
						 data-value="left"></view>
						<view :class="formats.align === 'center' ? 'ql-active' : ''" class="iconfont icon-juzhongduiqi" data-name="align"
						 data-value="center"></view>
						<view :class="formats.align === 'right' ? 'ql-active' : ''" class="iconfont icon-youduiqi" data-name="align"
						 data-value="right"></view>
						<view :class="formats.align === 'justify' ? 'ql-active' : ''" class="iconfont icon-zuoyouduiqi" data-name="align"
						 data-value="justify"></view>
						<view :class="formats.lineHeight ? 'ql-active' : ''" class="iconfont icon-line-height" data-name="lineHeight"
						 data-value="2"></view>
						<view :class="formats.letterSpacing ? 'ql-active' : ''" class="iconfont icon-Character-Spacing" data-name="letterSpacing"
						 data-value="2em"></view>
						<view :class="formats.marginTop ? 'ql-active' : ''" class="iconfont icon-722bianjiqi_duanqianju" data-name="marginTop"
						 data-value="20px"></view>
						<view :class="formats.previewarginBottom ? 'ql-active' : ''" class="iconfont icon-723bianjiqi_duanhouju"
						 data-name="marginBottom" data-value="20px"></view>
						<view class="iconfont icon-clearedformat" @tap="removeFormat"></view>
						<view :class="formats.fontFamily ? 'ql-active' : ''" class="iconfont icon-font" data-name="fontFamily" data-value="Pacifico"></view>
						<view :class="formats.fontSize === '24px' ? 'ql-active' : ''" class="iconfont icon-fontsize" data-name="fontSize"
						 data-value="24px"></view>
					
						<view :class="formats.color === '#0000ff' ? 'ql-active' : ''" class="iconfont icon-text_color" data-name="color"
						 data-value="#0000ff"></view>
						<view :class="formats.backgroundColor === '#00ff00' ? 'ql-active' : ''" class="iconfont icon-fontbgcolor"
						 data-name="backgroundColor" data-value="#00ff00"></view>
					
						<view class="iconfont icon-date" @tap="insertDate"></view>
						<view class="iconfont icon--checklist" data-name="list" data-value="check"></view>
						<view :class="formats.list === 'ordered' ? 'ql-active' : ''" class="iconfont icon-youxupailie" data-name="list"
						 data-value="ordered"></view>
						<view :class="formats.list === 'bullet' ? 'ql-active' : ''" class="iconfont icon-wuxupailie" data-name="list"
						 data-value="bullet"></view>
						<view class="iconfont icon-undo" @tap="undo"></view>
						<view class="iconfont icon-redo" @tap="redo"></view>
					
						<view class="iconfont icon-outdent" data-name="indent" data-value="-1"></view>
						<view class="iconfont icon-indent" data-name="indent" data-value="+1"></view>
						<view class="iconfont icon-fengexian" @tap="insertDivider"></view>
						<view class="iconfont icon-charutupian" @tap="insertImage"></view>
						<view :class="formats.header === 1 ? 'ql-active' : ''" class="iconfont icon-format-header-1" data-name="header"
						 :data-value="1"></view>
						<view :class="formats.script === 'sub' ? 'ql-active' : ''" class="iconfont icon-zitixiabiao" data-name="script"
						 data-value="sub"></view>
						<view :class="formats.script === 'super' ? 'ql-active' : ''" class="iconfont icon-zitishangbiao" data-name="script"
						 data-value="super"></view>
						<view class="iconfont icon-shanchu" @tap="clear"></view>
						<view :class="formats.direction === 'rtl' ? 'ql-active' : ''" class="iconfont icon-direction-rtl" data-name="direction"
						 data-value="rtl"></view>				
					</view>
				
					<view @click="openClose()"  class="opt">
						<view v-show="isOpen"><span>折叠</span></view>
						<view  v-show="!isOpen"><span>打开</span></view>
					</view>
				</view>
				
				<view class="day-active card">
						<view class="head"><input v-model="title" style="text-align: center;" placeholder="请输入标题"></input></view>
						<view @click="closePricture()" class="image-show">
							<image mode="aspectFill" v-if="picture!=null" class="active-img" :src="setUrl(picture)"></image>
							<span  v-else >点击选择封面</span>
						</view>
						<view class="active-warp">
							<view class="text-show dmid-front-14">
								<textarea placeholder="输入简介(120个字内)" :maxlength="120" style="width: 100%;" v-model="introduction"></textarea>
							</view>
						</view>
				</view>
				
				
				<editor  id="editor" class="ql-container" placeholder="请输入正文..." showImgSize showImgToolbar showImgResize
				 @statuschange="onStatusChange" :read-only="readOnly" @ready="onEditorReady">
				</editor>
				<view @click="ok()" class="send">
					<span v-if="loading==0">发送</span>
					<span v-else-if="loading==1">发送中</span>
					<span v-else>图片上传中</span>
					</view>
			</view>
		</view>

	</view>
</template>

<script>
import {uploadImage,saveOrUpdate} from "@/api/addTopic.js"
import { getUrl } from "../../utils/request";
import {getTopicById} from "@/api/index.js"
	export default {
		data() {
			return {
				loading2:0,
				loading:0,
				isOpen:1,
				top:0,
				id:null,
                readOnly: false,
				formats: {},
				title:null,
				picture:null,
				introduction:null,
				content:null
			}
		},	
		onLoad(op) {
			this.id = op.topicId;
			if(this.id!=undefined&&this.id!=null){
				this.loading2=1;
				 this.getTopicById(this.id).then(res=>{
					 this.loading2=0;
				 })
			}
			const query = uni.createSelectorQuery().in(this);
			query.select('.toolbar-box').boundingClientRect(data => {
				this.top = data.height
			}).exec();
			uni.loadFontFace({
				family: 'Pacifico',
				source: 'url("https://sungd.github.io/Pacifico.ttf")'
			})
		},
		methods: {
			/**
			 * 拿话题
			 * @param {Object} id
			 */
			 async getTopicById(id){
				
				const param = {
					topicId : id
				}
				const res = await getTopicById(param);
				
				this.title = res.data.title,
				this.content = res.data.content,
				this.introduction = res.data.introduction
				this.picture = res.data.picture
				
			},
			setUrl(url){
				return this.getUrl(url);
			},
			openClose(val){
				this.isOpen^=1;
			},
			closePricture(){
				
				uni.chooseImage({
					count:1,
					success: (e) => {
						const param = {
							name : "file",
							filePath: e.tempFilePaths[0]
						}
						this.loading=2;
						uploadImage(param).then((res)=>{
							this.picture = res.url
							this.loading=0;
						})
						
					},
					complete:(e)=> {
						this.loading = 0;
					},
				})
			},
			ok(){
				if(this.loading!=0)
					return ;
					
				if(this.stringUtils.isEmpty(this.title)){
					uni.showToast({
						icon:"none",
						title:"标题不能为空",
						duration:2000
					})
					return;
				}
				
				if(this.stringUtils.isEmpty(this.introduction)){
					uni.showToast({
						icon:"none",
						title:"简介不能为空",
						duration:2000
					})
					return;
				}
					
				this.loading = 1;	
				
				
				
				
				this.editorCtx.getContents({
					success:(e)=>{
						// console.log(e.html)
						// console.log(e)
						const params = {
							id:this.id,
							title:this.title,
							content:e.html,
							introduction:this.introduction,
							picture:this.picture,
							
						}
						
						// console.log(params)
						saveOrUpdate(params).then((res)=>{
							uni.showToast({
								title: '发布成功',
								duration: 2000
							});
							uni.navigateBack();
							
						}).catch(res=>{
							// uni.showToast({
							// 	title: '发布失败',
							// 	icon:"error",
							// 	duration: 2000
							// });
						})
						
						
						
					},
					fail(){
						
					},
					complete(){
						this.loading=0;
					}
					
				})
			},
			/**
			 * 仅读
			 */
			readOnlyChange() {
				this.readOnly = !this.readOnly
			},
			/**
			 * 初始化，获取上下文
			 */
			onEditorReady() {
				uni.createSelectorQuery().select('#editor').context((res) => {
					this.editorCtx = res.context
					this.editorCtx.setContents({
						html:this.content
					})
				}).exec()
				
				
			},
			/**
			 * 撤销和恢复
			 */
			undo() {
				this.editorCtx.undo()
			},
			redo() {
				this.editorCtx.redo()
			},
			/**
			 * 修改样式
			 * @param {Object} 
			 */
			format(e) {
				console.log(e)
				let {
					name,
					value
				} = e.target.dataset
				if (!name) return
				// console.log('format', name, value)
				this.editorCtx.format(name, value)
			},
			onStatusChange(e) {
				const formats = e.detail
				this.formats = formats
			},
			/**
			 * 插入分割线
			 */
			insertDivider() {
				this.editorCtx.insertDivider({
					success: function() {
						// console.log('insert divider success')
					}
				})
			},
			/**
			 * 清空
			 */
			clear() {
				this.editorCtx.clear({
					success: function(res) {
						// console.log("clear success")
					}
				})
			},
			/**
			 * 清除当前选区的样式
			 */
			removeFormat() {
				this.editorCtx.removeFormat()
			},
			/**
			 * 插入时间
			 */
			insertDate() {
				const date = new Date()
				const formatDate = `${date.getFullYear()}/${date.getMonth() + 1}/${date.getDate()}`
				//覆盖当前选区，设置一段文本
				this.editorCtx.insertText({
					text: formatDate
				})
			},
			/**
			 * 插入图片
			 */
			insertImage() {
				uni.chooseImage({
					count: 1,
					success: (res) => {
						
						const param = {
							name : "file",
							filePath:res.tempFilePaths[0]
						}
						this.loading = 2;
						uploadImage(param,(task)=>{
							// console.log(task)
							
							// task.onProgressUpdate((res=>{
							// 		console.log('上传进度' + res.progress);
							// 		console.log('已经上传的数据长度' + res.totalBytesSent);
							// 		console.log('预期需要上传的数据总长度' + res.totalBytesExpectedToSend);

							// }))
							
						}).then((res)=>{
							// console.log(res)
							this.editorCtx.insertImage({
								src: this.getUrl(res.url),
								alt: '图像',
								success: function() {
									// console.log('insert image success')
								}
							})
							this.loading = 0;
						}).catch(err=>{
							this.loading = 0;
						})
						
					}
				})
			}
		},
		
		watch:{
			isOpen(newVal,oldVal){
				console.log(newVal)
				this.$nextTick(()=>{
					const query = uni.createSelectorQuery().in(this);
					query.select('.toolbar-box').boundingClientRect(data => {
						
						this.top = data.height
						// this.$forceUpdate();
					}).exec();
				})
			
			}
		}
	}
</script>

<style>
	@import "/static/css/cs.css";
	.wrapper {
		
		position: relative;
		padding: 5px;
		
	}
	.iconfont {
		display: inline-block;
		padding: 8px 8px;
		width: 24px;
		height: 24px;
		cursor: pointer;
		font-size: 20px;
	}
	.toolbar-box {
		width: calc(100vw - 10px);
		position: fixed;
		top: 0;
		z-index: 100;
		background-color: white;
		box-sizing: border-box;
		border-bottom: 0;
		font-family: 'Helvetica Neue', 'Helvetica', 'Arial', sans-serif;
		box-shadow: rgba(6, 24, 44, 0.4) 0px 0px 0px 2px, rgba(6, 24, 44, 0.65) 0px 4px 6px -1px, rgba(255, 255, 255, 0.08) 0px 1px 0px inset;
		/* margin: 0 5px 0 5px; */
		border-radius: 0 0 5px 5px;
	}
	.ql-container {
		box-sizing: border-box;
		padding: 12px 15px;
		width: 100%;
		min-height: 30vh;
		height: auto;
		background: #fff;
		margin-top: 20px;
		font-size: 16px;
		line-height: 1.5;
	}
	.ql-active {
		color: #06c;
	}
	.opt{
		width: 100%;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		padding: 5px 0px;
		
		border-radius: 0 0 5px 5px;
		font-weight: 600;
	}
	/* 标题和简介样式 */
	/**/
	.day-active{
		position: relative;
		margin-top: 20px;
	}
	.day-active > .head{
		width: 33%;
		height: 60rpx;
		background-color: #fed23e;
		position: absolute;
		left: 50%;
		z-index: 10;
		transform: translate(-50%,-50%);
		display: flex;
		align-items: center;
		justify-content: center;
		border-radius: 5px;
	}
	.image-show{
		height: 300rpx;
		display: flex;
		justify-content: center;
		align-items: center;
	},
	.active-warp{
		
		margin: 0 20rpx;
	}
	.text-show{
		padding: 20px 0;
	}
	/*  标题和简介样式结束*/
	/* 图片 */
	.active-img{
		width: 100%;
		height: 300rpx;
		border-radius: 10px 10px 0 0;
	}
	/* 标题样式结束 */
	.send{
		width: 100%;
		height: 80rpx;
		display: flex;
		align-items: center;
		justify-content: center;
		background-color: #fccd43;
		border-radius: 10px;
	}
</style>

